@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

@font-face {
    font-family: 'Agatha';
    src: url('../fonts/Agatha.ttf');
}
@font-face {
    font-family: 'Attraction';
    src: url('../fonts/Attraction.ttf');
}
@font-face {
    font-family: 'LovelyCoffee';
    src: url('../fonts/LovelyCoffee.ttf');
}
@font-face {
    font-family: 'Maghrib';
    src: url('../fonts/Maghrib.ttf');
}
@font-face {
    font-family: 'Shathika';
    src: url('../fonts/Shathika.otf');
}

@tailwind base;
@tailwind components;
@tailwind utilities;
* {
    font-family: 'Noto Sans', sans-serif;
}
.font-cardo {
    font-family: 'Cardo', serif;
}
.font-agatha {
    font-family: 'Agatha';
}
.font-attraction {
    font-family: 'Attraction';
}
.font-maghrib {
    font-family: 'Maghrib';
}
.font-lovely-coffee {
    font-family: 'LovelyCoffee';
}
.font-shathika {
    font-family: 'Shathika';
}
.font-merriweather {
    font-family: 'Merriweather', serif;
}

.no-scrollbar::-webkit-scrollbar {
    display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.no-scrollbar {
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
}
